import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HOURS_REPORT } from '../../../../shared/interfaces/src/lib/hours-report/hours-report.config';
import { IDate, IReportCode } from '@shlomit/shared/interfaces';

@Injectable({
  providedIn: 'root',
})
export class HoursReportDataService {
 
  constructor(private httpClient: HttpClient) {}
  fetchData(date: IDate, userId?: string) {
    if (!userId) {
      userId = '44981';
    }
    date.month = +date.month < 10 ? `0${date.month}` : date.month;

    return this.httpClient.get(HOURS_REPORT.GET_USER_HOURREPORT(date, userId));
  }

  addreport(data: any) {
    return this.httpClient.post(HOURS_REPORT.ADD_REPORT(), data);
  }

  getreportcodes() {
    return this.httpClient.get(HOURS_REPORT.GET_REPORT_CODES());  }

}
