import { environment } from 'environments/environment';
import { IDate } from './date';

export const HOURS_REPORT = {
  GET_REPORT_CODES: (): string => {
    return `${environment.API_ENDPOINT.api}/api/hour/getreportcodes`;
  },

  GET_USER_HOURREPORT: (date: IDate, userId: string): string => {
    return `${environment.API_ENDPOINT.api}/api/hour/getuserhourreport?userIndex=${userId}&mnYear=${date.month}/${date.year}`;
  },

  ADD_REPORT: (): string => {
    return `${environment.API_ENDPOINT.api}/api/hour/addreport`;
  },
};
