import { Directive } from '@angular/core';
import { IReportCode } from '@shlomit/shared/interfaces';
import { FormBaseDirective } from '@shlomit/ui';
import { HoursReportService } from '../services/hours-report.service';
import { FormBuilder, AbstractControl } from '@angular/forms';
import { CustomValidators } from 'libs/ui/src/lib/form/error/error.validator';
import { IForm } from '../../../../shared/interfaces/src/lib/hours-report/form';

@Directive({
  selector: '[appForm]',
})
export class FormDirective extends FormBaseDirective<any> {
  constructor(
    public hoursReportService: HoursReportService,
    public fb: FormBuilder
  ) {
    super(
      fb.group({
        reportDate: ['', [CustomValidators.required]],
        toReportDate: ['', [CustomValidators.required]],
        reportStart: ['', [CustomValidators.required]],
        reportEnd: ['', [CustomValidators.required]],

        reportCode: ['', [CustomValidators.required]],
        locationStart: ['10.0.0.1', [CustomValidators.required]],
      })
    );
  }
  get reportCodes(): IReportCode[] {
    return this.hoursReportService.reportCodes;
  }

  // formatDate(this.post.startDate, 'yyyy-MM-dd', 'en')

  get reportDate(): AbstractControl {
    return this.form.get('reportDate');
  }

  onReportTypeChange(e) {
    console.log(e)

  }
  submit() {
    console.log(this.form.value);

    if (this.form.valid) {
      let data: IForm = this.form.value;
      data.userIndex = 44981;

      this.hoursReportService.addreport(data).subscribe((data) => {
        console.log(data);
      });
    }
  }
}
