import { Injectable } from '@angular/core';
import { IDate, IReportCode } from '@shlomit/shared/interfaces';
import { HoursReportDataService } from './hours-report-data.service';

@Injectable({
  providedIn: 'root',
})
export class HoursReportService {
  addreport(data: any) {
    return this.hoursReportDataService.addreport(data);
  }
  _reportCodes: IReportCode[];
  get reportCodes(): IReportCode[] {
    if (!this._reportCodes) {
      this.hoursReportDataService.getreportcodes().subscribe((data: any) => {
        this._reportCodes = <IReportCode[]>data.data;
      });
    }
    return this._reportCodes;
  }

  constructor(private hoursReportDataService: HoursReportDataService) {}

  fetchData(data: IDate, userId?: string) {
    return this.hoursReportDataService.fetchData(data, userId);
  }
}
