import { Routes, RouterModule } from '@angular/router';
import { HoursReportComponent } from './hours-report.component';
import { FormComponent } from '../form/form.component';
import { FilesComponent } from '../absence/files/files.component';

const routes: Routes = [
  {
    path: '',
    component: HoursReportComponent,
    children: [
      {
        path: 'month',
        component: HoursReportComponent,
        //         loadChildren: () => import('./form/form.component').then((m) => m.FormComponent),
      },
      {
        path: 'add',
        component: FormComponent,
        data: {
          showOffCanvase: true,
        },
      },
      {
        path: 'update/:id',
        component: FormComponent,
        data: {
          showOffCanvase: true,
        },
      },

      {
        path: 'update',
        component: FormComponent,
        data: {
          showOffCanvase: true,
        },
        //         loadChildren: () => import('./form/form.component').then((m) => m.FormComponent),
      },

      {
        path: 'files',
        component: FilesComponent,
        //         loadChildren: () => import('./form/form.component').then((m) => m.FormComponent),
      },
    ],
  },
];

export const HoursReportRoutes = RouterModule.forChild(routes);
