import { Component, OnInit } from '@angular/core';
import { NgbCalendar, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { HoursReportService } from '../services/hours-report.service';
import { ActivatedRoute, Router, RoutesRecognized } from '@angular/router';
import { animationFrameScheduler } from 'rxjs';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'app-hours-report',
  templateUrl: './hours-report.component.html',
  styleUrls: ['./hours-report.component.scss'],
})
export class HoursReportComponent implements OnInit {
  showOffCanvase = true;
  items;
  placeSherutName;
  private _date: { year: string; month: string } = {
    year: new Date().getFullYear().toString(),
    month: (new Date().getMonth() + 1).toString(),
  };
  get date() {
    return this._date;
  }
  set date(val) {
    this._date = val;
    this.hoursReportService.fetchData(this.date).subscribe((data) => {
      this.items = data;
      this.placeSherutName = data['data']['placeSherutName'];
    });
  }

  get reportDays() {
    return this.items.data.reportDays;
  }

  model: NgbDateStruct;
  constructor(
    private calendar: NgbCalendar,
    private hoursReportService: HoursReportService,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {}
  ngOnInit() {
    // debugger
  }
  onDateSelect(e) {
    this.date = e.next;
  }

  update() {}

  toggle() {
    this.showOffCanvase = !this.showOffCanvase;
  }
  open() {
    this.showOffCanvase = true;
  }
  close() {
    this.showOffCanvase = false;
  }
}
